* {
  margin: 0px;
  padding: 0px;
  font-family: 'Ubuntu', sans-serif;
}

:root {
  --color1: rgb(22, 18, 60);
  --color: #ffffff
}

#root {
  background: url("./bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.App {
  /* background-color: rgba(22, 18, 60, .5); */
  background-color: rgba(22, 18, 60, .5);
}

#signin-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#signin-btn {
  position: absolute;
  top: 50%;
  width: 100vw;
  display: flex;
  align-items: center;
  margin: auto;
  background-color: white;
  width: fit-content;
  padding: .5rem;
  border-radius: 1rem;
}

#signin-btn:hover {
  filter: opacity(60%);
}

#signin-btn:active {
  filter: opacity(40%);
}

#signin-btn img {
  width: 1.5rem;
  margin-right: .5rem;
  height: 1.5rem;
}

#signin-btn label {
  height: 100%;
  font-size: 1.25rem;
}

.chat-room {
  margin: auto;
  max-width: 800px;
  display: flex;
  flex-direction: column;
}

header {
  background-color: rgba(22, 18, 60, .8);
  border-radius: 1rem 1rem 0rem 0rem;
  display: flex;
  justify-content: space-between;
}

header h1 {
  color: white;
  margin: 1rem;
}

#signout-btn {
  height: 100%;
  padding: .5rem;
  margin: 1rem;
  justify-self: center;
  align-self: center;
}

#signout-btn:hover {
  filter: opacity(60%);
}

#signout-btn:active {
  filter: opacity(40%);
}

section {
  overflow-y: auto;
  background-color: rgba(43, 43, 44, 0.5);
  height: 80vh;
}

.msg-container {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.received {
  flex-direction: row;
}

.msg {
  word-wrap: break-word;
  border-radius: 1rem;
  padding: 1rem;
  max-width: 30rem;
  margin: .5rem;
  background-color: rgba(0, 0, 0, 0.4);
}

.display-name {
  font-weight: bold;
  color: rgb(22, 18, 60);
}

.sent .display-name {
  color: rgba(226, 132, 195, 0.6);
}

.received .display-name {
  color: rgb(152, 144, 219);
}

.msg-text {
  color: white;
}

.msg-container img {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  margin: .5rem;
}

#input-field {
  display: flex;
  background-color: rgba(43, 43, 44, 0.5);
  border-radius: 0rem 0rem 1rem 1rem;
}

#input-field input {
  border-radius: 1rem;
  width: 100%;
  height: 3rem;
  font-size: 2rem;
}

#input-field img {
  height: 3rem;
  width: 3rem;
  margin: 0rem 1rem;
}

#input-field img:hover {
  filter: opacity(60%);
}

#input-field img:active {
  filter: opacity(40%);
}